* {
    padding: 0;
    margin: 0;
}

#container {
    background-color: whitesmoke;
}

.main-section {
    max-width: 70vw;
}

.middle-section {
    max-width: 70vw;
    margin-inline: 20px;
    margin-bottom: 10px;
}

.middle-section .pie-chart {
    width: 240px;
    margin-inline: 20px;
    margin-bottom: 10px;
    padding-inline: 30px;
    background-color: white;
    border-radius: 10px;
}

.welcome-name {
    margin-block: 10px 15px;
    margin-right: 25px;
    margin-left: 40px;
}

.welcome-name input {
    border: none;
    border-radius: 0px 5px 5px 0px;
    padding: 8px 15px;
    outline: none;
}

.welcome-name img {
    width: 50px;
    height: 50px;
}

.search-logo {
    border-radius: 5px 0 0 5px;
    padding: 8px 0 8px 5px;
}

.chart-bar {
    width: 40px;
    border-radius: 10px;
}

.chart-container {
    width: 600px;
    height: 260px;
    background-color: white;
    padding: 20px;
    border-radius: 15px;
}

.chart-container .monthely-earning {
    font-size: 14px;
    color: #909090;
}

.chart-container .quarterly {
    background-color: #ede8e8;
    width: 80px;
    padding-inline: 7px;
    border-radius: 8px;
    font-size: 15px;
    color: #545454;
}

.chart {
    align-items: baseline;
}

.dashboard-option {
    color: white;
    padding: 10px 10px 10px 10px;
    border-radius: 10px;
}

.dashboard {
    height: 100vh;
    width: 15vw;
    padding-inline: 10px;
    background-color: #040440;
    color: white;
}

.dashboard-options-container {
    margin-left: 5px;
}

.dashboard-profile {
    background-color: #ffffff1a;
    padding: 10px;
    margin-bottom: 50px;
}

.dashboard-profile .profile-img {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    margin-right: 10px;
}

.data-box-container {
    width: 70vw;
    margin-bottom: 25px;
}

.data-box {
    width: 220px;
    height: 120px;
    background-color: white;
    border-radius: 10px;
}

.data-box>.logo-container {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    margin: 10px;
}

.data-content {
    height: 150px;
    width: 100px;
    align-items: baseline;
}

.products {
    background-color: white;
}

.products .inputs-container .drop-down,
.product-search-input {
    background-color: rgb(247, 237, 237);
    color: rgb(117, 108, 108);
    width: 130px;
    height: 30px;
    margin-right: 20px;
    padding-inline: 10px;
    border: none;
    outline: none;
    border-radius: 5px 5px 5px 5px;
}

.products .inputs-container .product-search-input {
    border-radius: 0 5px 5px 0;
}

.product-search-logo {
    border-radius: 5px 0 0 5px;
    padding: 8px 0px 6px 5px;
}

.product-list-item {
    width: 1000px;
    height: 80px;
    border-radius: 10px;
}

.product-list-item>img {
    width: 90px;
    height: 50px;
    border-radius: 5px;
}